.Hp {
    position: absolute;
    font-size: 32px;
    color: red;
    right: 20px;
    top: -75px;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
}

.Active {
    opacity: 1;
    right: 20px;
    top: -75px;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-animation-name: decreaseHpEffect;
            animation-name: decreaseHpEffect;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: .3s;
          animation-delay: .3s;
}
.EnemyActive {
    opacity: 1;
    right: 40px;
    top: 20px;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-animation-name: decreaseHpEffectUser;
            animation-name: decreaseHpEffectUser;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}

@-webkit-keyframes decreaseHpEffect {
    0%   {opacity: 1;right: 20px;top:-75px;-webkit-transform: scale(0);transform: scale(0);}
    25%  {opacity: 1;}
    50%  {opacity: 1;}
    100% {opacity: 0;right: 140px;top: -175px;-webkit-transform: scale(1);transform: scale(1);}
  }

@keyframes decreaseHpEffect {
    0%   {opacity: 1;right: 20px;top:-75px;-webkit-transform: scale(0);transform: scale(0);}
    25%  {opacity: 1;}
    50%  {opacity: 1;}
    100% {opacity: 0;right: 140px;top: -175px;-webkit-transform: scale(1);transform: scale(1);}
  }

  @-webkit-keyframes decreaseHpEffectUser {
    0%   {opacity: 1;right: 20px;top:-75px;-webkit-transform: scale(0);transform: scale(0);}
    25%  {opacity: 1;}
    50%  {opacity: 1;}
    100% {opacity: 0;right: -140px;top: -175px;-webkit-transform: scale(1);transform: scale(1);}
  }

  @keyframes decreaseHpEffectUser {
    0%   {opacity: 1;right: 20px;top:-75px;-webkit-transform: scale(0);transform: scale(0);}
    25%  {opacity: 1;}
    50%  {opacity: 1;}
    100% {opacity: 0;right: -140px;top: -175px;-webkit-transform: scale(1);transform: scale(1);}
  }


