.EnemyLook img{
    position: absolute;
    top: 245px;
    right: 6%;
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.IsAttacked {
    -webkit-animation-name: isAttackedEffect;
            animation-name: isAttackedEffect;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}

.EnemyAtacking {
  -webkit-animation-name: enemyAttacking;
          animation-name: enemyAttacking;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}

@-webkit-keyframes enemyAttacking {
  0%   {
    top: 245px;
    right: 6%;
  }
  /* 5%   {bottom: unset; left: unset} */
  100% {
    top: 357px;
    right: 70%;
  }
}

@keyframes enemyAttacking {
  0%   {
    top: 245px;
    right: 6%;
  }
  /* 5%   {bottom: unset; left: unset} */
  100% {
    top: 357px;
    right: 70%;
  }
}

@-webkit-keyframes isAttackedEffect {
    0%   {-webkit-filter: grayscale(0%);filter: grayscale(0%);}
    100% {-webkit-filter: grayscale(100%);filter: grayscale(100%);}
  }

@keyframes isAttackedEffect {
    0%   {-webkit-filter: grayscale(0%);filter: grayscale(0%);}
    100% {-webkit-filter: grayscale(100%);filter: grayscale(100%);}
  }


